.page {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;

  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  margin: 0 auto;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  background-color: #f5f6f7;
  background-repeat: no-repeat;
  background-size: cover;
  color: #cfcfcf;
}

.circle-preloader {
  display: block;
  width: 50px;
  height: 50px;
  border: 4px solid #444;
  border-bottom-color: #888;

  border-radius: 50%;

  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  margin: auto;

  -webkit-animation: spin 0.75s infinite linear;

          animation: spin 0.75s infinite linear;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  margin: 0 auto;
  padding: 28px 0 32px 27px;
  max-width: 320px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: flex-end;
  grid-gap: 20px;
  gap: 20px;
}

@media screen and (min-width: 550px) {
  .header {
    padding: 45px 0 41px;
    max-width: 500px;
  }
}

@media screen and (min-width: 920px) {
  .header {
    max-width: 880px;
  }
}

.header__logo {
  margin: 0;
  padding: 0;
  width: 126px;
  min-height: 24px;
}

@media screen and (min-width: 550px) {
  .header__logo {
    width: 173px;
    min-height: 33px;
  }
}

.header__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.searchform {
  margin: 36px auto 0;
  padding: 0 19px;
  max-width: 320px;
}

@media screen and (min-width: 550px) {
  .searchform {
    max-width: 500px;
  }
}

@media screen and (min-width: 920px) {
  .searchform {
    margin: 60px auto 0;
    max-width: 880px;
  }
}

.searchform__form {
  margin: 0 auto;
  padding-left: 8px;
  height: 64px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
  box-sizing: border-box;

  border-radius: 100px;
  background-color: #ffffff;
}

.searchform__input {
  width: 100%;
  border: #ffffff;
  border-radius: 30px;
  border: #ffffff;
  padding-left: 8px;
}

.searchform__input:focus {
  outline: none;
}

.searchform__input::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

.searchform__button {
  margin: 0 auto;
  padding: 0;
  width: 116px;
  border-radius: 100px;
  color: #ffffff;
  background-color: #2f71e5;
  border: #2f71e5;
  transition: background-color 0.4s ease-in-out;
}

.searchform__button:hover {
  background-color: #347eff;
}

@media screen and (min-width: 550px) {
  .searchform__button {
    width: 168px;
  }
}

.cards {
  margin: 0 auto;
  padding: 36px 19px 0;
  max-width: 320px;

  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  box-sizing: border-box;
}

@media screen and (min-width: 550px) {
  .cards {
    padding: 46.25px 0 0;
    display: grid;
    max-width: 500px;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(100px, 400px);
  }
}

@media screen and (min-width: 920px) {
  .cards {
    max-width: 880px;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}

.card {
  margin: 0 auto;
  width: 100%;
  height: 400px;
  position: relative;
  background-color: #ffffff;
  border: 0;
  border-radius: 16px;
  overflow: hidden;
}

.card__image {
  width: 100%;
  height: 60%;
  display: block;
  object-fit: cover;

  cursor: pointer;

  border-radius: 16px 16px 0 0;
}

.card__info {
  margin: 0 auto;
  padding: 12px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.card__title {
  margin: 0 auto;
  padding: 16px 16px 0;
  width: 220px;

  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  color: #1a1b22;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}

.card__description {
  margin: 0 auto;
  padding: 16px 16px 0;
  width: 220px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.details {
  margin: 60px auto;
  padding-bottom: 20px;

  border-radius: 16px;
  max-width: 320px;
  background-color: #eeeceb;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

@media screen and (min-width: 550px) {
  .details {
    max-width: 500px;
  }
}

@media screen and (min-width: 920px) {
  .details {
    max-width: 880px;
  }
}

.details__image {
  border-radius: 16px 16px 0 0;
}

@media screen and (min-width: 550px) {
  .details {
  }
}
@media screen and (min-width: 920px) {
  .details__image {
  }
}

.details__info {
  padding: 0 20px;
  color: #fff;

  color: #443f3c;
}

.details__title {
  padding: 0 20px;

  color: #443f3c;
}

@media screen and (min-width: 550px) {
  .details {
  }
}
@media screen and (min-width: 920px) {
  .details {
  }
}

.details__subtitle {
  padding: 0 20px;

  color: #443f3c;
}

.link {
  color: orangered;
  text-decoration: none;
  text-align: center;
}

.link:hover {
  opacity: 0.6;
}

.footer {
  margin: 0 auto;
  padding: 50px 19px 36px;
  max-width: 320px;
}

@media screen and (min-width: 550px) {
  .footer {
    padding: 68px 0 60px;
    max-width: 500px;
  }
}

@media screen and (min-width: 920px) {
  .footer {
    max-width: 880px;
  }
}

.footer__copyright {
  margin: 0 auto;
  padding: 0;
  text-align: center;
  font-size: 14px;
  line-height: 1.21;
  color: #cfcfcf;
}
@media screen and (min-width: 550px) {
  .footer__copyright {
    margin: 0;
    font-size: 1.8rem;
    line-height: 1.22;
  }
}



